<template>
  <div
    :id="`company-${companyData?.companyId}`"
    class="mb-spacing-500 flex flex-col-reverse gap-spacing-200 py-spacing-100 ipadPro:flex-row ipadPro:gap-spacing-0"
  >
    <div class="flex flex-[0.4] flex-col">
      <div
        v-if="companyData?.followUpHistory?.recentFollowUps?.length === 0"
        class="flex flex-1 items-center justify-center"
      >
        <div @click="startFollowUp(companyData)">
          <GeneralButton
            :button-type="'button'"
            :type="'brand'"
            :size="'small'"
            :emphasis="'secondary'"
            :icon-position="'none'"
            :label="'Start Follow Up'"
          />
        </div>
      </div>
      <div v-else>
        <div
          class="flex flex-col gap-spacing-100 px-spacing-200 py-spacing-100 ipad:px-spacing-300"
        >
          <div class="flex items-center gap-spacing-100">
            <p
              class="text-body/l/relaxed/400 text-content-on-neutral-secondary"
            >
              Status:
            </p>
            <p class="text-body/l/relaxed/400 text-content-on-neutral-primary">
              {{ companyData?.followUpHistory?.status }}
            </p>
          </div>
          <div class="flex items-center justify-between">
            <div class="flex items-center gap-spacing-100">
              <p
                class="text-body/l/relaxed/400 text-content-on-neutral-secondary"
              >
                Next Follow Up:
              </p>
              <p
                class="text-body/l/relaxed/400 text-content-on-neutral-primary"
              >
                {{ companyData?.followUpHistory?.nextFollowUpDate | toDate }}
              </p>
            </div>
            <div @click="startFollowUp(companyData)">
              <GeneralButton
                :button-type="'button'"
                :type="'success'"
                :size="'small'"
                :emphasis="'secondary'"
                :icon-position="'none'"
                :label="'Add'"
              />
            </div>
          </div>
        </div>
        <div
          class="mx-spacing-200 my-spacing-100 border-b border-constant-brand-primary ipad:mx-spacing-300"
        />
        <div class="flex gap-spacing-200">
          <ClassicTable :headers="followUpHeaders" :bg-class="'bg-transparent'">
            <template #table-body>
              <tr
                v-for="(followUp, index) in companyData?.followUpHistory
                  ?.recentFollowUps"
                :key="index"
                class="text-left text-body/m/relaxed/400 text-content-on-neutral-primary nexus:text-body/l/relaxed/400"
              >
                <td
                  class="flex flex-col gap-spacing-25 py-spacing-100 pr-spacing-100"
                >
                  {{ followUp?.date | toDate }}
                </td>
                <td class="py-spacing-100 pr-spacing-100">
                  {{ followUp?.note || "------------" }}
                </td>
              </tr>
            </template>
          </ClassicTable>
        </div>
        <div
          v-if="companyData?.followUpHistory?.hasMoreFollowUps"
          class="mt-spacing-100 px-spacing-200 ipad:px-spacing-300"
          @click="showMoreFollowUps(companyData)"
        >
          <GeneralButton
            :button-type="'button'"
            :type="'brand'"
            :size="'large'"
            :emphasis="'tertiary'"
            :icon-position="'none'"
            :label="'View More'"
          />
        </div>
      </div>
    </div>
    <div class="hidden w-0 border-r border-stroke-bold ipad:block" />
    <div
      class="flex-[0.6] text-body/l/relaxed/400 text-content-on-neutral-primary"
    >
      <p
        class="mb-spacing-150 flex items-center gap-spacing-100 px-spacing-200 text-body/l/relaxed/500 ipad:px-spacing-300"
      >
        <span
          class="cursor-pointer hover:text-hover-constant-brand-primary"
          @click.stop="goToCompanyDetailsPage(companyData?.companyId)"
          v-html="$highlightMatch(companyData?.companyName, searchQuery)"
        /><span class="h-0 flex-1 border-b border-constant-brand-primary" />
      </p>
      <ClassicTable
        :headers="headers"
        :bg-class="'bg-transparent'"
        :header-class="'first:pl-spacing-200 ipad:first:pl-spacing-300'"
        :horizontal-padding="'px-spacing-0'"
      >
        <template #table-body>
          <tr
            v-for="(product, index) in companyData?.products"
            :key="index"
            :id="
              product?.quotation?.variableId
                ? `company-${companyData?.companyId}-product-${product?.quotation?.variableId?._id}`
                : `company-${companyData?.companyId}-product-${product?.productId}`
            "
            class="text-left text-body/m/relaxed/400 text-content-on-neutral-primary nexus:text-body/l/relaxed/400"
            @click="
              product?.doesCompanySell
                ? addPriceForProduct(companyData, product)
                : undefined
            "
            :class="{
              'cursor-pointer hover:bg-bg-neutral-sunken':
                getProductDetailsStatus(product)?.price ===
                  productStatus.AVAILABLE ||
                getProductDetailsStatus(product)?.price ===
                  productStatus.NOT_ADDED,
            }"
          >
            <td
              class="py-spacing-100 pl-spacing-200 pr-spacing-100 ipad:pl-spacing-300"
            >
              <div class="flex flex-col gap-spacing-25">
                <span
                  :class="{
                    'text-content-on-neutral-tertiary':
                      getProductDetailsStatus(product)?.price ===
                      productStatus.NOT_PRODUCED,
                    'text-content-on-status-on-warning':
                      getProductDetailsStatus(product)?.price ===
                      productStatus.NOT_ADDED,
                  }"
                  >{{ product?.productName }}</span
                >

                <p
                  v-if="product?.quotation?.variableId"
                  class="min-h-spacing-100"
                >
                  <span
                    class="text-body/s/relaxed/400 italic nexus:text-body/m/relaxed/400"
                    :class="{
                      'text-content-on-neutral-secondary':
                        getProductDetailsStatus(product)?.price ===
                        productStatus.AVAILABLE,
                      'text-content-on-neutral-tertiary':
                        getProductDetailsStatus(product)?.price ===
                        productStatus.NOT_PRODUCED,
                      'text-content-on-status-on-warning':
                        getProductDetailsStatus(product)?.price ===
                        productStatus.NOT_ADDED,
                    }"
                  >
                    [{{ product?.quotation?.variableId?.name }},
                    {{ product?.quotation?.variableId?.coefficient }}]
                  </span>
                </p>
                <p v-else class="min-h-spacing-100">
                  <span
                    class="text-body/s/relaxed/400 italic nexus:text-body/m/relaxed/400"
                    :class="{
                      'text-content-on-neutral-secondary':
                        getProductDetailsStatus(product)?.price ===
                        productStatus.AVAILABLE,
                      'text-content-on-neutral-tertiary':
                        getProductDetailsStatus(product)?.price ===
                        productStatus.NOT_PRODUCED,
                      'text-content-on-status-on-warning':
                        getProductDetailsStatus(product)?.price ===
                        productStatus.NOT_ADDED,
                    }"
                  >
                    [default, 1]
                  </span>
                </p>
              </div>
            </td>
            <td
              v-if="
                getProductDetailsStatus(product)?.dateOfPriceAdded ===
                productStatus.AVAILABLE
              "
              class="py-spacing-100 pr-spacing-100"
            >
              {{ product?.quotation?.dateOfPriceAdded | toDate }}
            </td>
            <td
              v-else-if="
                getProductDetailsStatus(product)?.dateOfPriceAdded ===
                productStatus.NOT_PRODUCED
              "
              class="py-spacing-100 pr-spacing-100 text-content-on-neutral-tertiary"
            >
              {{ productStatus.NOT_PRODUCED }}
            </td>
            <td
              v-else-if="
                getProductDetailsStatus(product)?.dateOfPriceAdded ===
                productStatus.NOT_ADDED
              "
              class="py-spacing-100 pr-spacing-100 text-content-on-status-on-warning"
            >
              {{ productStatus.NOT_ADDED }}
            </td>

            <td
              v-if="
                getProductDetailsStatus(product)?.price ===
                productStatus.AVAILABLE
              "
              class="py-spacing-100 pr-spacing-100"
            >
              {{ product?.quotation?.price | toCurrency }}
            </td>
            <td
              v-else-if="
                getProductDetailsStatus(product)?.price ===
                productStatus.NOT_PRODUCED
              "
              class="py-spacing-100 pr-spacing-100 text-content-on-neutral-tertiary"
            >
              {{ productStatus.NOT_PRODUCED }}
            </td>
            <td
              v-else-if="
                getProductDetailsStatus(product)?.price ===
                productStatus.NOT_ADDED
              "
              class="py-spacing-100 pr-spacing-100 text-content-on-status-on-warning"
            >
              {{ productStatus.NOT_ADDED }}
            </td>

            <td
              v-if="
                getProductDetailsStatus(product)?.unit ===
                productStatus.AVAILABLE
              "
              class="py-spacing-100 pr-spacing-100"
            >
              {{ product?.productUnit | toCurrency }}
            </td>
            <td
              v-else-if="
                getProductDetailsStatus(product)?.unit ===
                productStatus.NOT_PRODUCED
              "
              class="py-spacing-100 pr-spacing-100 text-content-on-neutral-tertiary"
            >
              {{ productStatus.NOT_PRODUCED }}
            </td>
            <td
              v-else-if="
                getProductDetailsStatus(product)?.unit ===
                productStatus.NOT_ADDED
              "
              class="py-spacing-100 pr-spacing-100 text-content-on-status-on-warning"
            >
              {{ productStatus.NOT_ADDED }}
            </td>

            <td
              v-if="
                getProductDetailsStatus(product)?.score ===
                productStatus.AVAILABLE
              "
              class="py-spacing-100 pr-spacing-100"
            >
              {{ product?.quotation?.score | toPercentage(true) }}
            </td>
            <td
              v-else-if="
                getProductDetailsStatus(product)?.score ===
                productStatus.NOT_PRODUCED
              "
              class="py-spacing-100 pr-spacing-100 text-content-on-neutral-tertiary"
            >
              {{ productStatus.NOT_PRODUCED }}
            </td>
            <td
              v-else-if="
                getProductDetailsStatus(product)?.score ===
                productStatus.NOT_ADDED
              "
              class="py-spacing-100 pr-spacing-100 text-content-on-status-on-warning"
            >
              {{ productStatus.NOT_ADDED }}
            </td>

            <td
              v-if="
                getProductDetailsStatus(product)?.quotation ===
                productStatus.AVAILABLE
              "
              class="py-spacing-100 pr-spacing-100"
            >
              <DownloadAndView :file="product?.quotation?.quotationFile" />
            </td>
            <td
              v-else-if="
                getProductDetailsStatus(product)?.quotation ===
                productStatus.NOT_PRODUCED
              "
              class="py-spacing-100 pr-spacing-100 text-content-on-neutral-tertiary"
            >
              {{ productStatus.NOT_PRODUCED }}
            </td>
            <td
              v-else-if="
                getProductDetailsStatus(product)?.quotation ===
                productStatus.NOT_ADDED
              "
              class="py-spacing-100 pr-spacing-100 text-content-on-status-on-warning"
            >
              {{ productStatus.NOT_ADDED }}
            </td>
          </tr>
        </template>
      </ClassicTable>
    </div>
  </div>
</template>

<script>
export default {
  components: {
    ClassicTable: () => import("@/components/General/Table/ClassicTable.vue"),
    GeneralButton: () => import("@/components/General/CustomButton.vue"),
    DownloadAndView: () => import("@/components/Quotation/DownloadAndView.vue"),
  },
  props: {
    companyData: {
      type: Object,
      required: true,
    },
    marketResearchData: {
      type: Object,
      required: true,
    },
    searchQuery: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      followUpHeaders: [
        { key: "updateDate", value: "Last Update", sortable: false },
        { key: "note", value: "Note", sortable: false },
      ],
      headers: [
        { key: "productName", value: "Product Name", sortable: false },
        { key: "dateOfPriceAdded", value: "Date Input", sortable: false },
        { key: "price", value: "Price", sortable: false },
        { key: "unit", value: "Unit", sortable: false },
        { key: "score", value: "Score", sortable: false },
        { key: "quotation", value: "Quotation", sortable: false },
      ],
      productStatus: Object.freeze({
        NOT_ADDED: "To be added",
        NOT_PRODUCED: "Not produced",
        AVAILABLE: "Available",
      }),
    };
  },
  methods: {
    getProductDetailsStatus(product) {
      let output = {
        dateOfPriceAdded: this.productStatus.NOT_ADDED,
        price: this.productStatus.NOT_ADDED,
        unit: this.productStatus.NOT_ADDED,
        score: this.productStatus.NOT_ADDED,
        quotation: this.productStatus.NOT_ADDED,
      };
      if (!isNaN(parseFloat(product?.quotation?.price))) {
        output.price = output.unit = this.productStatus.AVAILABLE;
      }
      if (product?.quotation?.score) {
        output.score = this.productStatus.AVAILABLE;
      }
      if (product?.quotation?.quotationFile) {
        output.quotation = this.productStatus.AVAILABLE;
      }
      if (product?.quotation?.dateOfPriceAdded) {
        output.dateOfPriceAdded = this.productStatus.AVAILABLE;
      }
      if (!product?.doesCompanySell) {
        for (const key in output) {
          output[key] = this.productStatus.NOT_PRODUCED;
        }
      }
      return output;
    },

    startFollowUp(companyData) {
      this.openStatusUpdateModal(companyData);
    },
    goToCompanyDetailsPage(companyId) {
      this.$router.push(`/dashboard/companies/${companyId}`);
    },
    openStatusUpdateModal(companyData) {
      this.$bus.$emit("research-status-modal", {
        showModal: true,
        companyData,
        marketResearchId: this.marketResearchData?._id,
        marketResearchSettings: {
          ...this.marketResearchData?.settings,
          dateStart: this.marketResearchData?.dateStart,
        },
        statusOptions: this.marketResearchData?.statusOptions,
      });
    },
    addPriceForProduct(companyData, product) {
      this.$bus.$emit("research-status-modal", {
        showModal: true,
        companyData,
        marketResearchId: this.marketResearchData?._id,
        productId: product?.productId,
        marketResearchSettings: {
          ...this.marketResearchData?.settings,
          dateStart: this.marketResearchData?.dateStart,
        },
        statusOptions: this.marketResearchData?.statusOptions,
      });
    },
    showMoreFollowUps(companyData) {
      this.$bus.$emit("more-follow-ups-modal", {
        showModal: true,
        companyId: companyData?.companyId,
        marketResearchId: this.marketResearchData?._id,
      });
    },
  },
};
</script>
